#logo {
  font-size: 1.5em;
  font-family: "Hanken Grotesk Black";
}

#athena-nav p, a {
  font-family: "Hanken Grotesk";
  color: black;
  font-size: 1em;
}

#athena-nav {
  padding-top: 2vh;
  padding-left: 10vw;
  padding-right: 10vw;
  background-color: #F3EAFD;
}

@font-face {
  font-family: "Hanken Grotesk";
  src: url(/static/media/HankenGrotesk-Regular.8aa432c0.eot);
  src:url(/static/media/HankenGrotesk-Regular.a3d1b1bb.woff)format("woff"),
  url(/static/media/HankenGrotesk-Regular.9e8bb12f.otf)format("opentype")
}

@font-face {
  font-family: "Hanken Grotesk Black";
  src: url(/static/media/HankenGrotesk-Black.499224d6.eot);
  src:url(/static/media/HankenGrotesk-Black.ea8cbdeb.woff)format("woff"),
  url(/static/media/HankenGrotesk-Black.616709b8.otf)format("opentype")
}

@font-face {
  font-family: "Hanken Grotesk Medium";
  src: url(/static/media/HankenGrotesk-Medium.11157450.eot);
  src:url(/static/media/HankenGrotesk-Medium.7ed163a6.woff)format("woff"),
  url(/static/media/HankenGrotesk-Medium.15c02b41.otf)format("opentype")
}

#about-row {
  height: 85vh;
  background-color: #F3EAFD;
  width: 100%;
  padding-left: 11vw;
  padding-right: 11vw;
}

#about-h {
  margin-top: 20vh;
}

#athena-hero {
  margin-top: 10vh;
}

#small-descriptor {
  border-right: solid 1px white;
  max-height: 10vh;
}

#small-descriptor p, #second-small-descriptor  p {
  font-size: 1vw;
  color: white;
}

@media screen and (max-width: 600px) {
  #about-row {
    height: 15em;
  }

  #about-text {
    padding-top: 5%;
  }

  #about-button {
    font-size: 2.5vw;
  }

  #small-descriptor {
    height: 15vh;
  }

  #small-descriptor p, #second-small-descriptor  p {
    font-size: 2vw;
    color: white;
  }
}

#pathways-row {
  background-color: white;
  width: 100%;
  padding-top: 2vh;
  padding-left: 11vw;
  padding-right: 11vw;
  padding-bottom: 5vh;
}

#pathways-header {
  text-align: center;
}

#pathways-row img {
  padding: 2vw;
}

#path-cards p {
  font-size: 0.9em;
}

#path-cards {
  padding-top: 5vh;
  padding-left: 10vw;
}

#path-card {
  height: 70vh;
  width: 20vw;
  border: 1px solid #F3EAFD;
  box-shadow: 0 0 15px 0 #F3EAFD;
  margin-right: 2vw;
}

#stats-header {
  text-align: center;
}

#stats-row {
  background-color: #F3EAFD;
  width: 100%;
  padding-top: 2vh;
  padding-left: 11vw;
  padding-right: 11vw;
  padding-bottom: 5vh;
}

#stats {
  margin-top: 5vh;
  height: 50vh;
  width: 50vw;
  background: #FFFFFF;
  box-shadow: 0 0 15px 0 #A097A9;
}

#footer-row {
  padding-top: 5vh;
  padding-left: 11vw;
  padding-right: 11vw;
}

#footer-row h4 {
  font-size: 0.8em;
}
#footer-row p, ol {
  font-size: 0.6em;
  list-style-type: none;
}

#company-row, #school-row {
  padding-top: 5vh;
  padding-left: 10vw;
  padding-right: 10vw;
}

#company-header, #school-header {
  text-align: center;
  padding-top: 10vh;
  height: 25vh;
  background-color: #F3EAFD;
}

.athena-small-widget {
  height: 45vh;
  background: url(/static/media/rit.99ccfd66.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-color: #F2F1F4;
}

.modal-table {
  margin-top: 5%;
  margin-left: 10%;
  margin-bottom: 5%;
}

.modal-table td {
  padding-left: 2%;
  width: 25%;
  border-left: 1px solid black;
}

.athena-modal-header h3 {
  padding-left: 12%;
}

.athena-modal-body h4 {
  padding-left: 12%;
}

.athena-modal-body h5 {
  padding-left: 12%;
  font-size: 1em;
  color: #505051;
}

.athena-modal-body p {
  color: #505051;
}

.athena-modal-footer {
  padding-right: 45%;
}

#card-body {
  height: 10vh;
  color: white;
  background: rgba(154, 141, 164, 0.9);
}

.modal-table p {
  font-size: 1em;
}

.athena-small-widget p {
  font-size: 0.9em;
}

.athena-small-widget h3 {
  text-align: center;
  padding-top: 25%;
  font-size: 1.5em;
}

.ranking-badge {
  font-size: 1.2em;
  padding-left: 5%;
  padding-right: 5%;
  background-color: inherit;
  border-bottom: solid 2.5px white;
}

.modal-table .no-border {
  border: none;
}

.review {
  font-style: italic;
}

.add-new-button {
  margin-top: 5%;
}

@media screen and (max-width: 600px) {
  .athena-small-widget h3 {
    font-size: 1em;
  }

  .athena-small-widget {
    height: 20vh;
  }

  .athena-small-widget h3 {
    padding-top: 10%;
    padding-bottom: 15%;
  }

}

.sign-up, .sign-in {
  margin-top: 5%;
  border: solid 1px black;
  padding: 2%;
}

Form {
  font-family: "Hanken Grotesk";
}

Form p {
  font-family: "Hanken Grotesk";
  font-size: 0.8em;
  color: red;
}

.sign-up h5, .sign-in h5 {
  font-family: "Hanken Grotesk";
  font-size: 0.8em;
  padding: 1%;
}

Form .athena-primary {
  margin-right: 2%;
}

.page-title {
  margin-top: 2%;
  margin-bottom: 2%;
  text-align: center;
}

.athena-form {
  border: 0.5px solid grey;
  padding: 5%;
  margin-top: 2%;
}

.athena-radio {
  display: -webkit-inline-flex;
  display: inline-flex;
  padding-left: 8%;
}

.rating-table {
  padding-top: 5%
}

.athena-question {
  font-weight: bolder;
}

html, body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Hanken Grotesk", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

div, Button, .ranking-badge, Input {
  border-radius: 0 !important;
}

h1 {
  font-family: "Hanken Grotesk Black";
  font-size: 2.5em;
}

h2 {
  font-family: "Hanken Grotesk Black";
  font-size: 2.5em;
}

h3 {
  font-family: "Hanken Grotesk Black";
  font-size: 1.2em;
}

h5, p {
  font-family: "Hanken Grotesk";
  font-size: 1em;
}

.athena-primary {
  font-family: "Hanken Grotesk Medium";
  background-color: black;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 1.4em;
  padding-left: 10%;
  padding-right: 10%;
}

.athena-secondary {
  font-family: "Hanken Grotesk Medium";
  background-color: #9A8DA4;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 1.4em;
  padding-left: 10%;
  padding-right: 10%;
}

.athena-primary-white {
  font-family: "Hanken Grotesk Medium";
  background-color: white;
  border: none;
  color: black;
  text-align: center;
  text-decoration: none;
  font-size: 1.4em;
  padding-left: 10%;
  padding-right: 10%;
}

